export const LitMap = (dict, key) => 
    (part) => {
        part.setValue(key);
        const k = key.split('.');
        key = k[0];
        if (k[1]) {
            const indx = k[1];
            if (!dict[key]) {
                dict[key] = [];
            }
            dict[key][indx] = part.committer.element;
        } else {
            dict[key] = part.committer.element; 
        }
    }